'use client'
import React, { useState, useEffect } from 'react';
import { Calendar, Share2, CheckCircle, Trophy, Users, Brain, HeartHandshake, GraduationCap, Code, Star, Clock, Target, ArrowRight, Award, BookOpen, Globe, Sparkles } from 'lucide-react';

const NPLCoder = () => {
  const [shareText, setShareText] = useState("Share the event");
  const [isCopied, setIsCopied] = useState(false);
  const [isVisible, setIsVisible] = useState(false);
  const [countdown, setCountdown] = useState({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0
  });

  useEffect(() => {
    setIsVisible(true);
    const deadline = new Date('2025-01-10T12:00:00-08:00').getTime();

    const updateCountdown = () => {
      const now = new Date().getTime();
      const distance = deadline - now;

      setCountdown({
        days: Math.floor(distance / (1000 * 60 * 60 * 24)),
        hours: Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
        minutes: Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)),
        seconds: Math.floor((distance % (1000 * 60)) / 1000)
      });
    };

    updateCountdown();
    const interval = setInterval(updateCountdown, 1000);
    return () => clearInterval(interval);
  }, []);

  const handleShare = async () => {
    try {
      await navigator.clipboard.writeText(window.location.href);
      setShareText("Link copied!");
      setIsCopied(true);
      setTimeout(() => {
        setShareText("Share the event");
        setIsCopied(false);
      }, 2000);
    } catch (err) {
      console.error("Failed to copy:", err);
      setShareText("Failed to copy");
      setTimeout(() => setShareText("Share the event"), 2000);
    }
  };

  return (
    <div className="min-h-screen bg-white dark:bg-gradient-to-br dark:from-[#0d0f15] dark:via-[#2C3043] dark:to-[#1a1d2c]">
      {/* Hero Section */}
      <div className="relative overflow-hidden bg-gradient-to-b from-purple-900 to-blue-900 text-white">
        <div className="absolute inset-0">
          <div className="absolute inset-0 bg-[url('/api/placeholder/1920/1080')] opacity-10"></div>
          <div className="absolute inset-0 bg-gradient-to-r from-purple-900/50 to-blue-900/50"></div>
        </div>
        
        <div className="relative container mx-auto px-4 py-24 lg:py-32">
          <div className={`max-w-4xl mx-auto text-center transform transition-all duration-1000 ${isVisible ? 'translate-y-0 opacity-100' : 'translate-y-10 opacity-0'}`}>
            <div className="inline-block mb-6 bg-white/10 backdrop-blur-lg rounded-full px-6 py-2">
              <div className="flex items-center space-x-2">
                <Sparkles className="w-5 h-5 text-yellow-400" />
                <span className="text-sm font-medium">Registration Now Open</span>
              </div>
            </div>
            
            <h1 className="text-5xl lg:text-7xl font-bold mb-6 bg-clip-text text-transparent bg-gradient-to-r from-purple-200 to-blue-200">
              NPLCoder | Nepal Data Challenge
            </h1>
            <p className="text-2xl lg:text-3xl mb-8 text-gray-300">
              Where Passion Meets Possibility
            </p>
            <p className="text-xl text-gray-300 mb-12 max-w-2xl mx-auto">
              Join a transformative journey in AI and Machine Learning, where collaboration and mentorship create the leaders of tomorrow.
            </p>
            
            <div className="flex flex-wrap justify-center gap-4 mb-12">
              <button className="px-8 py-4 bg-gradient-to-r from-purple-500 to-blue-500 rounded-xl font-semibold hover:opacity-90 transform hover:scale-105 transition-all shadow-lg">
                Sign Up Now
              </button>
              <button
                onClick={handleShare}
                className={`px-8 py-4 backdrop-blur-lg rounded-xl font-semibold flex items-center gap-2 transition-all transform hover:scale-105 ${
                  isCopied
                    ? "bg-green-500 text-white"
                    : "bg-white/10 hover:bg-white/20"
                }`}
              >
                {isCopied ? <CheckCircle className="w-5 h-5" /> : <Share2 className="w-5 h-5" />}
                {shareText}
              </button>
            </div>

            {/* Countdown Timer */}
            <div className="inline-flex gap-4 bg-white/10 backdrop-blur-lg rounded-2xl p-6">
              <div className="text-center">
                <div className="text-3xl font-bold">{countdown.days}</div>
                <div className="text-sm text-gray-300">Days</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold">{countdown.hours}</div>
                <div className="text-sm text-gray-300">Hours</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold">{countdown.minutes}</div>
                <div className="text-sm text-gray-300">Minutes</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold">{countdown.seconds}</div>
                <div className="text-sm text-gray-300">Seconds</div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="container mx-auto px-4">
        {/* Introduction */}
        <div className="py-20">
          <div className="max-w-4xl mx-auto text-center">
            <h2 className="text-3xl lg:text-4xl font-bold mb-8 text-gray-900 dark:text-white">
              Building Nepal's AI Future, Together
            </h2>
            <p className="text-xl text-gray-600 dark:text-gray-300 mb-8">
              At NPLCoder, we believe that the future of technology in Nepal lies in our collective effort to grow, support, and uplift one another. Through mentorship, collaboration, and a commitment to helping each other, we aim to create lifelong professional networks and foster an environment where all members can thrive.
            </p>
          </div>
        </div>

        {/* Key Features */}
        <div className="py-20">
          <h2 className="text-3xl lg:text-4xl font-bold text-center mb-16 text-gray-900 dark:text-white">
            Why Join NPLCoder?
          </h2>
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
            {[
              {
                icon: <GraduationCap className="w-8 h-8 text-purple-500" />,
                title: "Expert Mentorship",
                description: "Learn from industry leaders and experienced professionals who will guide your journey."
              },
              {
                icon: <Globe className="w-8 h-8 text-blue-500" />,
                title: "Global Network",
                description: "Connect with peers and professionals from around the world, building lasting relationships."
              },
              {
                icon: <Brain className="w-8 h-8 text-purple-500" />,
                title: "Skill Development",
                description: "Master cutting-edge AI/ML technologies through hands-on projects and challenges."
              },
              {
                icon: <HeartHandshake className="w-8 h-8 text-blue-500" />,
                title: "Collaborative Spirit",
                description: "Experience the power of teamwork in a supportive, growth-focused environment."
              },
              {
                icon: <Target className="w-8 h-8 text-purple-500" />,
                title: "Career Opportunities",
                description: "Access exclusive job opportunities and graduate school preparation resources."
              },
              {
                icon: <Award className="w-8 h-8 text-blue-500" />,
                title: "Recognition & Rewards",
                description: "Compete for Rs. 25,000 in prizes and gain recognition in Nepal's tech community."
              }
            ].map((feature, index) => (
              <div key={index} className="bg-white dark:bg-gray-800/50 rounded-xl p-8 shadow-lg backdrop-blur-lg hover:transform hover:scale-105 transition-all">
                <div className="mb-6">{feature.icon}</div>
                <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-4">{feature.title}</h3>
                <p className="text-gray-600 dark:text-gray-300">{feature.description}</p>
              </div>
            ))}
          </div>
        </div>

        {/* Evaluation Criteria */}
        <div className="py-20">
          <div className="bg-gradient-to-r from-purple-900 to-blue-900 rounded-2xl p-12 text-white">
            <h2 className="text-3xl lg:text-4xl font-bold mb-12 text-center">Evaluation Criteria</h2>
            <div className="grid md:grid-cols-3 gap-8">
              <div className="text-center">
                <div className="bg-white/10 rounded-xl p-6 backdrop-blur-lg">
                  <Code className="w-12 h-12 mx-auto mb-4 text-purple-300" />
                  <h3 className="text-xl font-semibold mb-4">Technical Excellence</h3>
                  <p className="text-gray-300">Demonstrate your mastery of AI/ML concepts and implementation</p>
                </div>
              </div>
              <div className="text-center">
                <div className="bg-white/10 rounded-xl p-6 backdrop-blur-lg">
                  <BookOpen className="w-12 h-12 mx-auto mb-4 text-blue-300" />
                  <h3 className="text-xl font-semibold mb-4">Innovation</h3>
                  <p className="text-gray-300">Show creative problem-solving and unique approaches to challenges</p>
                </div>
              </div>
              <div className="text-center">
                <div className="bg-white/10 rounded-xl p-6 backdrop-blur-lg">
                  <Users className="w-12 h-12 mx-auto mb-4 text-purple-300" />
                  <h3 className="text-xl font-semibold mb-4">Collaboration</h3>
                  <p className="text-gray-300">Contribute to the community's growth and support fellow participants</p>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Prize Section */}
        <div className="py-20">
          <div className="bg-white dark:bg-gray-800/50 rounded-2xl p-12 shadow-lg backdrop-blur-lg">
            <div className="text-center mb-12">
              <Trophy className="w-16 h-16 mx-auto mb-6 text-yellow-500" />
              <h2 className="text-3xl lg:text-4xl font-bold text-gray-900 dark:text-white mb-4">Grand Prize</h2>
              <p className="text-4xl lg:text-5xl font-bold bg-gradient-to-r from-purple-600 to-blue-600 bg-clip-text text-transparent">
                Rs. 25,000
              </p>
            </div>
          </div>
        </div>

        {/* CTA Section */}
        <div className="py-20">
          <div className="max-w-4xl mx-auto text-center">
            <h2 className="text-3xl lg:text-4xl font-bold mb-8 text-gray-900 dark:text-white">
              Ready to Begin Your Journey?
            </h2>
            <p className="text-xl text-gray-600 dark:text-gray-300 mb-12">
              Join a community of passionate learners and innovators shaping the future of AI in Nepal.
            </p>
            <button className="px-12 py-6 bg-gradient-to-r from-purple-500 to-blue-500 text-white rounded-xl font-semibold text-xl hover:opacity-90 transform hover:scale-105 transition-all shadow-lg">
              Register Now
            </button>
          </div>
        </div>

        {/* Footer */}
        
      </div>
    </div>
  );
};

export default NPLCoder;